<template>
	<div class="pos">
			<el-row>
				<el-col :span='7' class="pos-order" id="order-list">
					<el-tabs type="card">
						<el-tab-pane label="点餐">
							<el-table :data="tableData" border style="width: 100%">
     							<el-table-column prop="goodsName" label="商品名称" > </el-table-column>
     							<el-table-column prop="count" label="数量" width="70"> </el-table-column>
     							<el-table-column prop="price" label="金额" width="70"> </el-table-column>
     							<el-table-column label="操作" width="90"> 
     									<template scope="scope">
     											<el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
     											<el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
     									</template>
     							</el-table-column>
							</el-table>
							<p><small>数量：</small>{{totalCount}}  <small>金额：</small>￥{{totalMoney}}元</p>
							<div class="pos-btn">
								<el-button type="success" round @click="checkout">结账</el-button>
								<el-button type="warning" round>挂起</el-button>
								<el-button type="danger" round @click="delAllGoods">删除</el-button>
							</div>
						</el-tab-pane>
						<el-tab-pane label="挂单">
							挂单
						</el-tab-pane>
						<el-tab-pane label="外卖">
							外卖
						</el-tab-pane>
					</el-tabs>
				</el-col>
				
				<el-col :span='17'>
					<div class="often-goods">
						<div class="title">常用商品</div>
						<div class="often-goods-list">
							<ul>
								<li v-for="goods in oftenGoods" v-on:click="addOrderList(goods)">
									<span>{{goods.goodsName}}</span>
									<span class="o-price">￥{{goods.price}}元</span>
								</li>
							</ul>
						</div>
					</div>
					<div class="goods-type">
						<el-tabs type="card">
							<el-tab-pane label="汉堡">
								<ul class="cookList">
									<li v-for="item in type0Goods" v-on:click="addOrderList(item)">
										<span class="foodImg"><img :src="item.goodsImg" width="100%"/></span>
										<span class="foodName">{{item.goodsName}}</span>
										<span class="foodPrice">￥{{item.price}}元</span>
									</li>
								</ul>
							</el-tab-pane>
							<el-tab-pane label="小食">
								<ul class="cookList">
									<li v-for="item in type1Goods" v-on:click="addOrderList(item)">
										<span class="foodImg"><img :src="item.goodsImg" width="100%"/></span>
										<span class="foodName">{{item.goodsName}}</span>
										<span class="foodPrice">￥{{item.price}}元</span>
									</li>
								</ul>
							</el-tab-pane>
							<el-tab-pane label="饮料">
								<ul class="cookList">
									<li v-for="item in type2Goods" v-on:click="addOrderList(item)">
										<span class="foodImg"><img :src="item.goodsImg" width="100%"/></span>
										<span class="foodName">{{item.goodsName}}</span>
										<span class="foodPrice">￥{{item.price}}元</span>
									</li>
								</ul>
							</el-tab-pane>
							<el-tab-pane label="套餐">
								<ul class="cookList">
									<li v-for="item in type3Goods" v-on:click="addOrderList(item)">
										<span class="foodImg"><img :src="item.goodsImg" width="100%"/></span>
										<span class="foodName">{{item.goodsName}}</span>
										<span class="foodPrice">￥{{item.price}}元</span>
									</li>
								</ul>
							</el-tab-pane>
						</el-tabs>
					</div>	
				</el-col>
			</el-row>	
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		name:'Pos',
		//element是虚拟的要等所有的虚拟DOM加载完成只有再执行
		mounted:function(){
			var orderHeight=document.body.clientHeight;
			document.getElementById("order-list").style.height=orderHeight+"px";
		},
		data(){
			return {
				tableData:[],
				oftenGoods:[],
				type0Goods:[],
				type1Goods:[],
				type2Goods:[],
				type3Goods:[],
				totalCount:0,
				totalMoney:0
				
			}
		},
		created(){
			 axios.get('http://jspang.com/DemoApi/oftenGoods.php')
			 .then(response=>{
			 	this.oftenGoods=response.data;
			 })
			 .catch(error=>{
			 	alert("网络错误，无法访问");
			 }),
			 axios.get('http://jspang.com/DemoApi/typeGoods.php')
			 .then(response=>{
			 	this.type0Goods=response.data[0];
			 	this.type1Goods=response.data[1];
			 	this.type2Goods=response.data[2];
			 	this.type3Goods=response.data[3]
			 })
			 .catch(error=>{
			 	alert('网络错误，无法访问');
			 })
		},
		methods:{
			getAllMoney(){
				this.totalCount=0;
				this.totalMoney=0;
				this.tableData.forEach(element=>{
					this.totalCount+=element.count;
					this.totalMoney=this.totalMoney+(element.price*element.count)
				})
			},
			delSingleGoods(goods)
			{
				this.tableData=this.tableData.filter(o=>o.goodsId!=goods.goodsId);
				this.getAllMoney();
			},
			delAllGoods()
			{
				this.tableData=[];
				this.totalCount=0;
				this.totalMoney=0;
				
			},
			addOrderList(goods){
				
				var isHave=false;
				for(var i=0;i<this.tableData.length;i++)
				{
					if(goods.goodsId==this.tableData[i].goodsId)
						isHave=true;
				}
				if(isHave)
				{
					var arr=this.tableData.filter(o=>o.goodsId==goods.goodsId);
					arr[0].count++;
				}
				else{
					var newGoods={
						goodsId:goods.goodsId,
						goodsName:goods.goodsName,
						price:goods.price,
						count:1
					};
					this.tableData.push(newGoods);
				}
				this.getAllMoney();	
			},
			checkout()
			{
				if(this.totalCount!=0)
				{
					this.tableData=[];
					this.totalCount=0;
					this.totalMoney=0;
					this.$message({
						message:'结账成功，欢迎下次光临！',
						type:'success'
					})
				}
				else{
					this.$message.error("订单为空，无法结账！")
				}
			}
		}
	}
</script>

<style scoped>
.pos-order
{
	background-color: #f9fafc;
	border-right: 1px solid #c0ccda;
}
.pos-btn
{
	margin-top:20px;
	
}
.title
{
	height: 20px;
	border-bottom: 1px solid #d3dce6;
	background-color: #f9fafc;
	padding:10px;
	text-align: left;	
}

.often-goods-list ul li
{
	cursor: pointer;
	float: left;
	list-style: none;
	border:1px solid #e5e9f2;
	padding: 10px;
	margin: 5px;
	background-color: #FFFFFF;
}
.o-price
{
	color:#58b7ff;
}
.goods-type
{
	clear:both;
	padding-top:20px;
}
.cookList li
{
	cursor: pointer;
	list-style: none;
	width: 23%;
	border:1px solid #e5e9f2;
	height: auto;
	overflow: hidden;
	background-color: #fff;
	padding: 2px;
	float: left;
	margin: 2px;
}
.cookList li span
{
	display: block;
	float:left;	
}
.foodImg
{
	width: 40%;
}
.foodName
{
	font-size: 16px;
	padding-left: 10px;
	color:brown;
}
.foodPrice
{
	font-size: 16px;
	padding-left: 10px;
	padding-top: 10px;
}
</style>


